@keyframes effect {
  0% {
    transform: scale(1);
    box-shadow: 0 0 15px 1px transparent;
  }
  50% {
    transform: scale(0.975);
    box-shadow: 0 0 15px 1px #e3f2f8;
  }
  100% {
    transform: scale(1.005);
    box-shadow: 0 0 17px 0.5px #cdcdcd;
  }
}
